<!--记录血压-->
<template>
  <div class="edit_food_box" :style="{ backgroundColor: '#F5F6F9' }">
    <SimpleHeader name="手动录入"></SimpleHeader>

    <div class="div_pop_content">

      <van-cell title="测量时间" type="number" :value="timeValue" @click=" ()=> show_picker= true" is-link  :style="{ marginTop: '16px', marginBottom: '16px'}"/>

        <van-field
            v-model="lowValue"
            label="舒张压(低压mmHg)"
            placeholder=""
            input-align="right"
            type = "number"
        />

      <van-field
          v-model="heighValue"
          label="收缩压(高压mmHg)"
          placeholder=""
          input-align="right"
          type = "number"
      />
      <van-field
          v-model="pulseValue"
          label="脉搏(次/分)"
          placeholder=""
          input-align="right"
          type = "number"
      />

    </div>
    <!--保存-->
    <van-button @click="onSave" class="van-button--primary" type="primary" loading-text="加载中..." block>提交
    </van-button>

    <van-popup :show="show_picker" position="bottom" >
      <van-datetime-picker
          item-height="25"
          v-model="currentDate"
          type="datetime"
          :min-date="minDate"
          :max-date="maxDate"
          @cancel="()=> show_picker = false"
          @confirm="confirmDatePicker"
          :formatter="formatter"
      />
    </van-popup>
  </div>


</template>

<script>
import { Toast } from 'vant'
import { reactive, onMounted, toRefs, ref } from 'vue'
import moment from "moment";
import { useRouter } from "vue-router";
import SimpleHeader from "@/components/SimpleHeader";
import {addBooldRecord} from "@/service/bloodpressure";

export default {
  components: {
    SimpleHeader,
  },
  props: {},
  setup() {
    const router = useRouter()
    const lowValue = ref('');
    const heighValue = ref('')
    const pulseValue = ref('')
    onMounted(
        () => {
        }
    )

    // 通过reactive 可以初始化一个可响应的数据，与Vue2.0中的Vue.observer很相似
    const state = reactive({
      timeValue: moment(new Date()).format("YYYY-MM-DD HH:mm"),// 时间选择
      currentDate: new Date(),
      minDate: new Date(2020, 0, 1,0,1),
      maxDate: new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate(),
          new Date().getHours(),new Date().getMinutes()),
      searchvalue: '',
      show_picker: false,
    })
    const formatter = (type, val) =>{
      if (type === 'year')
        return `${val}年`;
      else if (type === 'month')
        return `${val}月`;
      else if( type === 'day')
        return `${val}日`;
      else if( type === 'hour')
        return `${val}时`;
      else if( type === 'minute')
        return `${val}分`;
      return val;
    }

    //点击时间选择器的确认按钮
    const confirmDatePicker = (value) =>{
      state.timeValue = moment(new Date(value)).format("YYYY-MM-DD HH:mm")
      console.log('点击时间选择器的确认按钮', state.drinkTime,value)
      state.show_picker = false
    }

    /**
     * 保存
     */
    const onSave = async () => {
      console.log("router",router)

      // const edit_low = document.getElementById("edit_low")
      const { timeValue } = state
      if(!lowValue.value || Number(lowValue.value)>200 || Number(lowValue.value)<10){
        Toast('舒张压应在10-200间！');
        return
      }else if(!heighValue.value || Number(heighValue.value)>300 || Number(heighValue.value)<40){
        Toast('收缩压应在40-300间！');
        return
      }else if( Number(heighValue.value)<Number(lowValue.value)){
        Toast('收缩压应高于舒张压！');
        return
      }

      const res = await addBooldRecord(heighValue.value,lowValue.value,pulseValue.value,moment(new Date(timeValue)).format("YYYY-MM-DD HH:mm:ss"),moment(new Date(timeValue)).format("YYYY-MM-DD"))
      const { data, code} = res
      if(code === 200){
        router.go(-1)
      }else{
        Toast("添加异常")
      }

    };
    const myContainer = document.querySelector('.my-container');
    // 在Vue3.0中，所有的数据和方法都通过在setup 中 return 出去，然后在template中使用
    return {
      ...toRefs(state),
      onSave,
      myContainer,
      confirmDatePicker,
      formatter,
      lowValue,
      heighValue,
      pulseValue
    }
  }
}
</script>

